import { useStaticQuery, graphql } from 'gatsby'

export const useMemojis = () => {
  const data = useStaticQuery(graphql`
    query MemojisQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "memojis/intro.png"
              "memojis/english.png"
              "memojis/mechanical.png"
              "memojis/multimedia.png"
              "memojis/management.png"
              "memojis/coding.png"
              "memojis/call-me.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(layout: CONSTRAINED, width: 320)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getMemojiByName = name => {
  return useMemojis().find(obj => obj.name === name)
}
