import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const StyledLink = styled(Link)`
  display: flex;
  align-items: center;
  gap: 15px;
  width: 100%;
  text-decoration: none;
  color: ${props => props.theme.grey2};
  :hover {
    color: ${props => props.theme.coldPrimary};
  }
`

const StyledA = styled.a`
  display: flex;
  align-items: center;
  gap: 15px;
  width: 100%;
  text-decoration: none;
  color: ${props => props.theme.grey2};
  :hover {
    color: ${props => props.theme.coldPrimary};
  }
`

export default function DecoratedLink ({
  href,
  to,
  fontAwsomeIconProp,
  children,
  iconSize = 'xl'
}) {
  if (to) {
    return (
      <StyledLink to={to}>
        <FontAwesomeIcon icon={fontAwsomeIconProp} size={iconSize} />
        {children}
      </StyledLink>
    )
  } else {
    return (
      <StyledA href={href} target='_blank'>
        <FontAwesomeIcon icon={fontAwsomeIconProp} size={iconSize} />
        {children}
      </StyledA>
    )
  }
}
