import { useStaticQuery, graphql } from 'gatsby'

export const useAppIcons = () => {
  const data = useStaticQuery(graphql`
    query AppIconsQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "app-icons/aiffinity.png"
              "app-icons/better-uxin.png"
              "app-icons/beveloper.png"
              "app-icons/cverc.png"
              "app-icons/mcfur.png"
              "app-icons/stw.png"
              "app-icons/yuanrun.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(layout: CONSTRAINED, height: 60)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getAppIconByName = name => {
  return useAppIcons().find(obj => obj.name === name)
}
