import { useStaticQuery, graphql } from 'gatsby'

export const useMiscPics = () => {
  const data = useStaticQuery(graphql`
    query MiscPicsQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "misc/apple-principles.png"
              "misc/contact-gmail.png"
              "misc/role-in-gotawolf.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(layout: CONSTRAINED)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getMiscPicByName = name => {
  return useMiscPics().find(obj => obj.name === name)
}
