import { useStaticQuery, graphql } from 'gatsby'

export const useSkillIcons = () => {
  const data = useStaticQuery(graphql`
    query SkillIconsQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "skill-icons/skill-icon-english.png"
              "skill-icons/skill-icon-mechanic.png"
              "skill-icons/skill-icon-multimedia.png"
              "skill-icons/skill-icon-management.png"
              "skill-icons/skill-icon-coding.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(width: 60)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getQueriedImageByName = name => {
  return useSkillIcons().find(obj => obj.name === name)
}
