import { useStaticQuery, graphql } from 'gatsby'

export const useCompanyLogos = () => {
  const data = useStaticQuery(graphql`
    query CompanyLogosQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "company-logos/jacky-school.png"
              "company-logos/schoolpal.png"
              "company-logos/apple-rainbow.png"
              "company-logos/csst-name.png"
              "company-logos/hello-world.png"
              "company-logos/dsme.png"
              "company-logos/stw.png"
              "company-logos/yuanrun.png"
              "company-logos/csst-logo.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(layout: CONSTRAINED, height: 25)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getCompanyLogoByName = name => {
  return useCompanyLogos().find(obj => obj.name === name)
}
