import { useStaticQuery, graphql } from 'gatsby'

export const useDesignedLogos = () => {
  const data = useStaticQuery(graphql`
    query DesignedLogosQuery {
      allFile(
        filter: {
          relativePath: {
            in: [
              "company-logos/stw.png"
              "company-logos/yuanrun.png"
              "company-logos/csst-logo.png"
            ]
          }
        }
      ) {
        nodes {
          childImageSharp {
            gatsbyImageData(layout: CONSTRAINED, height: 100)
          }
          name
        }
      }
    }
  `)

  return data.allFile.nodes
}

export const getDesignedLogoByName = name => {
  return useDesignedLogos().find(obj => obj.name === name)
}
